#product {
    display: flex;
    flex-direction: row;

    width: 100vw;
    height: 100vh;

    overflow: hidden;
}

#product .image-panel {
    flex: 1;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    padding: 100px 50px;

    background-color: black;
    background-color: rgb(20, 20, 20);
}

#product .image-panel .image {
    height: 100%;
    object-fit: contain;
}

#product .info-panel {
    flex: 1;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;

    padding: 100px 50px;

    transition: all 700ms;

    background-color: white;
}

#product .info-panel .panel {
    width: 100%;

    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

#product .info-panel .panel .name {
    color: black;
    font-size: 50px;
    font-weight: 900;
    line-height: 70px;
}