#header {
    width: 100%;

    position: fixed;
    top: 0px;
    left: 0px;

    padding: 20px 25px;

    display: flex;
    flex-direction: row;
    align-items: center;

    z-index: 10;
}

#header img {
    width: 50px;
    object-fit: contain;
}

#header .menu {
    display: flex;
    flex-direction: row;
    align-items: center;
}

#header .menu .cart-icon {
    width: 20px;
    height: 20px;

    margin-bottom: 3px;
    margin-right: 20px;

    color: black;
}

#header .menu .menu-icon {
    width: 25px;
    height: 25px;

    color: black;
}