#landing {
    position: relative;

    display: flex;
    flex-direction: column;
    align-items: center;

    padding-top: 80px;
}

#landing .logo {
    width: 80px;

    margin-bottom: 20px;

    z-index: 2;
}

#landing .title {
    color: black;
    font-size: 55px;
    font-weight: 900;
    line-height: 55px;
}

#landing .background-image {
    position: absolute;
    left: 5%;
    bottom: 30px;

    width: 90%;
}