#home .product {
    display: flex;
    flex-direction: row;

    overflow: hidden;
}

#home .background {
    background-color: rgba(0, 0, 0, 0.05);
}

#home .product .info-panel {
    flex: 1;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    padding: 0px 25px 0px 100px;
}

#home .product .info-panel .panel {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

#home .product .info-panel .panel .heading {
    color: #BFD600;
    font-size: 25px;
    font-weight: 500;
}

#home .product .info-panel .panel .name {
    color: black;
    font-size: 65px;
    font-weight: 900;
    line-height: 70px;
}

#home .product .info-panel .panel .text {
    color: black;
    font-size: 20px;
    font-weight: 300;
    line-height: 30px;

    opacity: 0.5;
}

#home .product .info-panel .panel .properties {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

#home .product .info-panel .panel .item {
    display: flex;
    flex-direction: row;
    align-items: center;
}

#home .product .info-panel .panel .item .info {
    color: black;
    font-size: 16px;
    font-weight: 500;
    line-height: 16px;
}

#home .product .info-panel .panel .properties .item ion-icon {
    color: white;

    width: 16px;
    height: 16px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    padding: 8px;
    margin-right: 15px;

    border-radius: 50%;

    background-color: #BFD600;
}

#home .product .image-panel {
    flex: 1;

    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    padding: 0px 100px 0px 25px;
}

#home .product .image-panel .image {
    width: 100%;
    height: 80%;
    object-fit: contain;
}