:root {
    --animate-delay: 0.5s;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translate3d(0, 50px, 0);
  }

  to {
    opacity: 1;
    transform: none;
  }
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translate3d(0, -20px, 0);
  }

  to {
    opacity: 1;
    transform: none;
  }
}

@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translate3d(-50px, 0, 0);
  }

  to {
    opacity: 1;
    transform: none;
  }
}
* {
    margin: 0px;
    padding: 0px;

    box-sizing: border-box;

    font-family: "Helvetica Now Display", sans-serif;
    font-family: "Roboto", sans-serif;
}

::-webkit-scrollbar {
    display: none;
}

.green-color {
    color: #BFD600;
}

.green-background {
    background-color: #BFD700;
}
#header {
    width: 100%;

    position: fixed;
    top: 0px;
    left: 0px;

    padding: 20px 25px;

    display: flex;
    flex-direction: row;
    align-items: center;

    z-index: 10;
}

#header img {
    width: 50px;
    object-fit: contain;
}

#header .menu {
    display: flex;
    flex-direction: row;
    align-items: center;
}

#header .menu .cart-icon {
    width: 20px;
    height: 20px;

    margin-bottom: 3px;
    margin-right: 20px;

    color: black;
}

#header .menu .menu-icon {
    width: 25px;
    height: 25px;

    color: black;
}
#transition {
    width: 100vw;
    height: 0px;

    position: fixed;
    left: 0px;
    bottom: 0px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    overflow: hidden;

    transition: all 600ms ease-in-out;

    background-color: #BFD600;
    background-color: black;
    background-color: rgb(20, 20, 20);

    z-index: 100;
}

#transition img {
    width: 200px;
    height: 200px;
    object-fit: contain;
}
#landing {
    position: relative;

    display: flex;
    flex-direction: column;
    align-items: center;

    padding-top: 80px;
}

#landing .logo {
    width: 80px;

    margin-bottom: 20px;

    z-index: 2;
}

#landing .title {
    color: black;
    font-size: 55px;
    font-weight: 900;
    line-height: 55px;
}

#landing .background-image {
    position: absolute;
    left: 5%;
    bottom: 30px;

    width: 90%;
}
.button {
    display: flex;
    flex-direction: row;
    align-items: center;

    cursor: pointer;
}

.button:hover .title {
    margin-right: 20px;
}

.button .title {
    color: black;
    font-size: 18px;
    font-weight: 700;

    margin-right: 15px;

    transition: all 200ms;
}

.button ion-icon {
    color: #BFD600;

    width: 20px;
    height: 20px;

    margin-top: 2px;

    transition: all 200ms;
}

.button .white {
    color: white;
}

.button .black {
    color: black;
}
#home .product {
    display: flex;
    flex-direction: row;

    overflow: hidden;
}

#home .background {
    background-color: rgba(0, 0, 0, 0.05);
}

#home .product .info-panel {
    flex: 1 1;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    padding: 0px 25px 0px 100px;
}

#home .product .info-panel .panel {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

#home .product .info-panel .panel .heading {
    color: #BFD600;
    font-size: 25px;
    font-weight: 500;
}

#home .product .info-panel .panel .name {
    color: black;
    font-size: 65px;
    font-weight: 900;
    line-height: 70px;
}

#home .product .info-panel .panel .text {
    color: black;
    font-size: 20px;
    font-weight: 300;
    line-height: 30px;

    opacity: 0.5;
}

#home .product .info-panel .panel .properties {
    display: flex;
    flex-direction: column;
    grid-gap: 15px;
    gap: 15px;
}

#home .product .info-panel .panel .item {
    display: flex;
    flex-direction: row;
    align-items: center;
}

#home .product .info-panel .panel .item .info {
    color: black;
    font-size: 16px;
    font-weight: 500;
    line-height: 16px;
}

#home .product .info-panel .panel .properties .item ion-icon {
    color: white;

    width: 16px;
    height: 16px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    padding: 8px;
    margin-right: 15px;

    border-radius: 50%;

    background-color: #BFD600;
}

#home .product .image-panel {
    flex: 1 1;

    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    padding: 0px 100px 0px 25px;
}

#home .product .image-panel .image {
    width: 100%;
    height: 80%;
    object-fit: contain;
}
#home {
    width: 100vw;
    height: 100vh;

    overflow: hidden;
}

#home .container {
    overflow: scroll;
    height: 100vh;

    -ms-scroll-snap-type: mandatory;

        scroll-snap-type: mandatory;
    -ms-scroll-snap-points-y: repeat(100vh);
        scroll-snap-points-y: repeat(100vh);
    -ms-scroll-snap-type: y mandatory;
        scroll-snap-type: y mandatory;

    -webkit-scroll-snap-type: mandatory;
    -webkit-scroll-snap-points-y: repeat(100vh);
    -webkit-scroll-snap-type: y mandatory;
}

#home .screen {
    width: 100vw;
    height: 100vh;

    scroll-snap-align: start;
    -webkit-scroll-snap-align: start;
}

#scrollbar {
    width: 6px;
    height: 50%;

    position: fixed;
    right: 25px;
    top: 25%;

    border-radius: 10px;

    background-color: rgb(230, 230, 230);
}

#scrollbar .scrollbar-container {
    width: 100%;
    height: 100%;

    position: relative;
}

#scrollbar .scrollbar-container .indicator {
    width: 100%;

    position: absolute;
    left: 0px;
    top: 0px;

    border-radius: 10px;

    background-color: #BFD600;
}
#product {
    display: flex;
    flex-direction: row;

    width: 100vw;
    height: 100vh;

    overflow: hidden;
}

#product .image-panel {
    flex: 1 1;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    padding: 100px 50px;

    background-color: black;
    background-color: rgb(20, 20, 20);
}

#product .image-panel .image {
    height: 100%;
    object-fit: contain;
}

#product .info-panel {
    flex: 1 1;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;

    padding: 100px 50px;

    transition: all 700ms;

    background-color: white;
}

#product .info-panel .panel {
    width: 100%;

    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

#product .info-panel .panel .name {
    color: black;
    font-size: 50px;
    font-weight: 900;
    line-height: 70px;
}
