    padding: 0px;

    box-sizing: border-box;

    font-family: "Helvetica Now Display", sans-serif;
    font-family: "Roboto", sans-serif;
}

::-webkit-scrollbar {
    display: none;
}

.green-color {
    color: #BFD600;
}

.green-background {
    background-color: #BFD700;
}