.button {
    display: flex;
    flex-direction: row;
    align-items: center;

    cursor: pointer;
}

.button:hover .title {
    margin-right: 20px;
}

.button .title {
    color: black;
    font-size: 18px;
    font-weight: 700;

    margin-right: 15px;

    transition: all 200ms;
}

.button ion-icon {
    color: #BFD600;

    width: 20px;
    height: 20px;

    margin-top: 2px;

    transition: all 200ms;
}

.button .white {
    color: white;
}

.button .black {
    color: black;
}