#transition {
    width: 100vw;
    height: 0px;

    position: fixed;
    left: 0px;
    bottom: 0px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    overflow: hidden;

    transition: all 600ms ease-in-out;

    background-color: #BFD600;
    background-color: black;
    background-color: rgb(20, 20, 20);

    z-index: 100;
}

#transition img {
    width: 200px;
    height: 200px;
    object-fit: contain;
}